cAeM,SAAU,CAMf,CAAA","file":"application.scss","sourcesContent":["// http://meyerweb.com/eric/tools/css/reset/\n// v2.0 | 20110126\n// License: none (public domain)\n@mixin meyer-reset {\n  html,\n  body,\n  div,\n  span,\n  applet,\n  object,\n  iframe,\n  h1,\n  h2,\n  h3,\n  h4,\n  h5,\n  h6,\n  p,\n  blockquote,\n  pre,\n  a,\n  abbr,\n  acronym,\n  address,\n  big,\n  cite,\n  code,\n  del,\n  dfn,\n  em,\n  img,\n  ins,\n  kbd,\n  q,\n  s,\n  samp,\n  small,\n  strike,\n  strong,\n  sub,\n  sup,\n  tt,\n  var,\n  b,\n  u,\n  i,\n  center,\n  dl,\n  dt,\n  dd,\n  ol,\n  ul,\n  li,\n  fieldset,\n  form,\n  label,\n  legend,\n  table,\n  caption,\n  tbody,\n  tfoot,\n  thead,\n  tr,\n  th,\n  td,\n  article,\n  aside,\n  canvas,\n  details,\n  embed,\n  figure,\n  figcaption,\n  footer,\n  header,\n  hgroup,\n  menu,\n  nav,\n  output,\n  ruby,\n  section,\n  summary,\n  time,\n  mark,\n  audio,\n  video {\n    margin: 0;\n    padding: 0;\n    border: 0;\n    font-size: 100%;\n    font: inherit;\n    vertical-align: baseline;\n  } // HTML5 display-role reset for older browsers\n  article,\n  aside,\n  details,\n  figcaption,\n  figure,\n  footer,\n  header,\n  hgroup,\n  menu,\n  nav,\n  section {\n    display: block;\n  }\n  body {\n    line-height: 1;\n  }\n  ol,\n  ul {\n    list-style: none;\n  }\n  blockquote,\n  q {\n    quotes: none;\n  }\n  blockquote {\n    &:before,\n    &:after {\n      content: \"\";\n      content: none;\n    }\n  }\n  q {\n    &:before,\n    &:after {\n      content: \"\";\n      content: none;\n    }\n  }\n  table {\n    border-collapse: collapse;\n    border-spacing: 0;\n  }\n}\n\n@include meyer-reset;\n","@font-face {\n  font-family: \"CenturyGothic\";\n  src: url($font_path +\"CenturyGothic.eot\");\n  src: url($font_path +\"CenturyGothic.woff2\") format(\"woff2\"), url($font_path +\"CenturyGothic.woff\") format(\"woff\"), url($font_path +\"CenturyGothic.ttf\") format(\"truetype\"), url($font_path +\"CenturyGothic.svg#CenturyGothic\") format(\"svg\"), url($font_path +\"CenturyGothic.eot?#iefix\") format(\"embedded-opentype\");\n  font-weight: normal;\n  font-style: normal;\n}\n\n@font-face {\n  font-family: \"CenturyGothic\";\n  src: url($font_path +\"CenturyGothic-Bold.eot\");\n  src: url($font_path +\"CenturyGothic-Bold.woff2\") format(\"woff2\"), url($font_path +\"CenturyGothic-Bold.woff\") format(\"woff\"), url($font_path +\"CenturyGothic-Bold.ttf\") format(\"truetype\"), url($font_path +\"CenturyGothic-Bold.svg#CenturyGothic-Bold\") format(\"svg\"), url($font_path +\"CenturyGothic-Bold.eot?#iefix\") format(\"embedded-opentype\");\n  font-weight: bold;\n  font-style: normal;\n}\n","* {\n  transform-origin: 50% 50%;\n  transform-box: fill-box;\n  overflow: visible;\n  box-sizing: border-box;\n}\n\nhtml {\n  font-size: 16px;\n  -webkit-text-size-adjust: 100%;\n  /* Font varient */\n  font-variant-ligatures: none;\n  -webkit-font-variant-ligatures: none;\n  /* Smoothing */\n  text-rendering: optimizeLegibility;\n  -moz-osx-font-smoothing: grayscale;\n  font-smoothing: antialiased;\n  -webkit-font-smoothing: antialiased;\n  background: white;\n  margin: 0;\n  font-family: \"CenturyGothic\";\n}\n\nbody {\n  position: relative;\n  padding: 1.5rem 1.5rem 0;\n  padding: 2vmax 2vmax 0;\n  color: $black;\n  overflow-x: hidden;\n  height: 100%;\n  overflow: visible;\n  &.about-page-opened {\n    overflow: hidden;\n  }\n}\n\n#app {\n  position: relative;\n  min-height: 100vh;\n  min-height: calc(100vh - 1.5rem);\n  min-height: calc(100vh - 2vmax);\n  width: 100%;\n  background: $offwhite;\n  display: flex;\n  overflow: hidden;\n  @include tall {\n    flex-direction: column;\n    padding: 0 3vmax;\n  }\n  @include wide {\n    flex-direction: row;\n  }\n  &.invert-colors {\n    background: $soft-black;\n  }\n  z-index:1;\n  >* {\n    //z-index: 1;\n  }\n  .cover-image-wrap {\n    z-index: -1;\n  }\n}\n\n.page {\n  min-height: 100%;\n  @include tall {\n    position: relative;\n    flex: 1;\n    .pagewrap {\n      margin-top: 3vh;\n    }\n  }\n  @include wide {\n    width: 55vw;\n    padding: 18vh 4vw 10vh 4.75vw;\n    &.page-work {\n      padding: 18vh 4.5vw 10vh 3.25vw;\n    }\n    margin-left: auto;\n  }\n}\n","$font_path: '../assets/fonts/';\n$base-font: 1em;\n$black:#18171D;\n$pink:#F58092;\n$blue:#353544;\n$red:#CE374F;\n$yellow: #F7B267;\n$offwhite: #f5f5f5;\n$soft-black:#201f1f; //dark theme\n@mixin wide {\n  @media (orientation: landscape) {\n    @content;\n  }\n}\n\n@mixin tall {\n  @media (orientation: portrait) {\n    @content;\n  }\n}\n","h1,\nh2,\nh3,\nh4,\nh5 {\n  font-weight: bold;\n}\n\nh2 {\n  color: $blue;\n}\n\np,\na {\n  line-height: 1.5;\n  letter-spacing: -1px;\n  @include wide {\n    font-size: 3vw;\n    @media(min-width: 1024px) {\n      font-size: 2vw;\n    }\n  }\n  @include tall {\n    font-size: 5.5vw;\n    line-height: 1;\n    margin-bottom: 1.5vh;\n  }\n}\n\na {\n  color: $red;\n  text-decoration: none;\n  .text-links & {\n    position: relative;\n    &:before {\n      position: absolute;\n      top: 0;\n      left: 0;\n      height: 100%;\n      text-decoration: underline;\n      color: $red;\n      width: 0;\n      overflow: hidden;\n      transition: width .2s ease-out;\n    }\n    &:hover {\n      &:before {\n        width: 100%;\n      }\n    }\n    &.email {\n      &:before {\n        content: \"carterdanielr@gmail.com\";\n      }\n    }\n    &.phone {\n      &:before {\n        content: \"513.519.7464\";\n      }\n    }\n  }\n}\n","@keyframes heroCircleAlt {\n  0% {\n    -webkit-transform: rotate3d(0, 0, 0) scale(1);\n    transform: rotate3d(0, 0, 0) scale(1);\n  }\n  25% {\n    -webkit-transform: rotate3d(-0.25, -0.5, -0.25, 90deg) scale(1);\n    transform: rotate3d(-0.25, -0.5, -0.25, 90deg) scale(1);\n  }\n  50% {\n    -webkit-transform: rotate3d(-0.5, -1, -0.5, 180deg) scale(1);\n    transform: rotate3d(-0.5, -1, -0.5, 180deg) scale(1);\n  }\n  75% {\n    -webkit-transform: rotate3d(-0.75, -1.5, -0.75, 270deg) scale(1);\n    transform: rotate3d(-0.75, -1.5, -0.75, 270deg) scale(1);\n  }\n  100% {\n    -webkit-transform: rotate3d(-1, -2, -1, 360deg) scale(1);\n    transform: rotate3d(-1, -2, -1, 360deg) scale(1);\n  }\n}\n\n@keyframes heroCircle {\n  0% {\n    -webkit-transform: rotate3d(0, 0, 0) scale(1);\n    transform: rotate3d(0, 0, 0) scale(1);\n  }\n  25% {\n    -webkit-transform: rotate3d(0.25, 0.5, 0.25, 90deg) scale(1);\n    transform: rotate3d(0.25, 0.5, 0.25, 90deg) scale(1);\n  }\n  50% {\n    -webkit-transform: rotate3d(0.5, 1, 0.5, 180deg) scale(1);\n    transform: rotate3d(0.5, 1, 0.5, 180deg) scale(1);\n  }\n  75% {\n    -webkit-transform: rotate3d(0.75, 1.5, 0.25, 270deg) scale(1);\n    transform: rotate3d(0.75, 1.5, 0.25, 270deg) scale(1);\n  }\n  100% {\n    -webkit-transform: rotate3d(1, 2, 1, 360deg) scale(1);\n    transform: rotate3d(1, 2, 1, 360deg) scale(1);\n  }\n}\n\n@keyframes heroCircleAltShort {\n  0% {\n    -webkit-transform: rotate3d(0, 0, 0) scale(1);\n    transform: rotate3d(0, 0, 0) scale(1);\n  }\n  50% {\n    -webkit-transform: rotate3d(-0.5, -1, -0.5, 180deg) scale(1);\n    transform: rotate3d(-0.5, -1, -0.5, 180deg) scale(1);\n  }\n  100% {\n    -webkit-transform: rotate3d(-1, -2, -1, 360deg) scale(1);\n    transform: rotate3d(-1, -2, -1, 360deg) scale(1);\n  }\n}\n\n@keyframes heroCircleShort {\n  0% {\n    -webkit-transform: rotate3d(0, 0, 0) scale(1);\n    transform: rotate3d(0, 0, 0) scale(1);\n  }\n  50% {\n    -webkit-transform: rotate3d(0.5, 1, 0.5, 180deg) scale(1);\n    transform: rotate3d(0.5, 1, 0.5, 180deg) scale(1);\n  }\n  100% {\n    -webkit-transform: rotate3d(1, 2, 1, 360deg) scale(1);\n    transform: rotate3d(1, 2, 1, 360deg) scale(1);\n  }\n}\n\n@keyframes fromTop {\n  from {\n    transform: translate3d(0, -100vmax, 0);\n    opacity: 0;\n  }\n  to {\n    transform: translate3d(0, 0, 0);\n    opacity: 1;\n  }\n}\n\n@keyframes fromRight {\n  from {\n    transform: translate3d(100vmax, 0, 0);\n    opacity: 0;\n  }\n  to {\n    tra